.checker {
  font-size: 3vw;
  cursor: -webkit-grab;
  cursor: grab;
  border: 0;
  margin: 0;
  -webkit-transform: "translate3d(0,0,0)";
          transform: "translate3d(0,0,0)";
}

.checker.translucent {
  opacity: 0.6;
}

.checker.moveable {
  -webkit-animation: grow 0.5s infinite;
          animation: grow 0.5s infinite;
}

@-webkit-keyframes grow {
  0% {
    -webkit-transform: rotate(0deg);
  }
  25% {
    -webkit-transform: rotate(2deg);
  }
  50% {
    -webkit-transform: rotate(-2deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
  }
}

@keyframes grow {
  0% {
    -webkit-transform: rotate(0deg);
  }
  25% {
    -webkit-transform: rotate(2deg);
  }
  50% {
    -webkit-transform: rotate(-2deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
  }
}



